import Model from "../Model";
import { IRequest } from "./requests/IRequest";


class Controller {
    private Model: Model;

    constructor() {
        this.Model = new Model();
    }

    getModel() {
        return this.Model
    }

    async excecuteRequest(request: IRequest): Promise<void> {
        return request.execute(this.Model)
    }
}

export default Controller